<template>
  <div>
    <el-form :model="tranDefForm"
             :rules="rules"
             label-suffix=""
             :label-width="formLabelWidth"
             ref="tranDefForm"
             style="text-align: left;padding-right: 5em;">
      <el-form-item label="" prop="parent_tab" class="hidden">
        <el-input v-model="tranDefForm.parent_tab" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="tab_name" class="hidden">
        <el-input v-model="tranDefForm.tab_name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="modelused_o" class="hidden">
        <el-input v-model="tranDefForm.modelused_o" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="traindate" class="hidden">
        <el-input v-model="tranDefForm.traindate" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="op" class="hidden">
        <el-input v-model="tranDefForm.op" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="tab_desc">
        <el-input v-model="tranDefForm.tab_desc" auto-complete="off" :readonly="formReadonly"></el-input>
      </el-form-item>
      <el-form-item label="" prop="show_order">
        <el-input v-model.number="tranDefForm.show_order" auto-complete="off" :readonly="formReadonly"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-radio v-model="tranDefForm.txn_type" label="0" :disabled="formReadonly"></el-radio>
        <el-radio v-model="tranDefForm.txn_type" label="1" :disabled="formReadonly"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="txnid" v-bind:class="{hidden:txnIdVisible}">
        <el-input v-model="tranDefForm.txnid" auto-complete="off" :readonly="formReadonly"></el-input>
      </el-form-item>
      <el-form-item label=":" :label-width="formLabelWidth" prop="tab_disposal">
        <AllPickSelect :dataList="tabDisposalList" @dataChange="addStatParamDataChange" :selectedList="selectedList" :disabled="formReadonly"></AllPickSelect>
      </el-form-item>
      <el-form-item label="" prop="chann">
        <el-select v-model="tranDefForm.chann" placeholder="" :disabled="formReadonly||channVisible">
          <el-option
            v-for="item in channelList"
            :key="item.channelid"
            :label="item.channelname"
            :value="item.channelid">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="txnffname">
        <el-input v-model="tranDefForm.txnffname" auto-complete="off" readonly="readonly"></el-input>
      </el-form-item>
      <el-form-item label="" prop="modelused" v-bind:class="{hidden:modelusedVisible}">
        <el-select v-model="tranDefForm.modelused" placeholder="" :disabled="formReadonly">
          <el-option
            v-for="item in modelUsedList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-radio v-model="tranDefForm.is_enable" label="1" :disabled="formReadonly"></el-radio>
        <el-radio v-model="tranDefForm.is_enable" label="0" :disabled="formReadonly"></el-radio>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import AllPickSelect from '@/components/common/AllPickSelect'
  import check from '@/common/check'

  export default {
    create () {
    },
    computed: {
      selectedList: function () { // 
        let tabDisposal = this.tranDefForm.tab_disposal
        if (!tabDisposal || tabDisposal.trim() === '') {
          return []
        } else {
          return tabDisposal.split(',')
        }
      },
      txnIdVisible: function () { // 
        return this.tranDefForm.txn_type !== '1'
      },
      channVisible: function () { // ()
        var parChann = this.tranDefForm.par_chann
        return !(parChann === null || parChann === '' || undefined === parChann)
      },
      modelusedVisible: function () { // :0
        return this.tranDefForm.op === 'add'
      },
      formReadonly () { // 
        return this.tranDefForm.tab_name === 'T' || (this.tranDefForm.tab_name === '' && this.tranDefForm.op === '')
      }
    },
    data () {
      var txnIdCheck = (rule, value, callback) => {
        var self = this
        if (value.trim() === '' && self.tranDefForm.txn_type === '1') {
          return callback(new Error(','))
        } else {
          callback()
        }
      }
      var channCheck = (rule, value, callback) => {
        var self = this
        if (value.trim() === '' && self.tranDefForm.txn_type === '1') {
          return callback(new Error(','))
        } else {
          callback()
        }
      }
      return {
        tabDisposalList: [],
        tabDisposalSelectedCached: [],
        modelUsedList: [{label: '', value: '0'}, {label: '', value: '1'}],
        channelList: [{channelname: '', channelid: '0'}],
        tranDefForm: {
          parent_tab: '',
          tab_name: '',
          modelused_o: '',
          traindate: '',
          op: '', // addmod
          tab_desc: '', // 
          show_order: '', // 
          txn_type: '0', // 
          txnid: '', // 
          tab_disposal: '', // 
          chann: '', // 
          par_chann: '', // 
          txnffname: '', // 
          modelused: '', // 
          is_enable: '1' // 
        },
        formLabelWidth: '120px',
        rules: {
          txnid: [
            {max: 20, message: '20', trigger: 'blur'},
            {validator: check.checkFormEnSpecialCharacter, trigger: 'blur'}, // ,
            {validator: txnIdCheck, trigger: 'blur'} // ;
          ],
          chann: [
            {validator: channCheck, trigger: 'blur'} // ;
          ],
          disposal: [
            {required: true, message: '', trigger: 'blur'}
          ],
          tab_desc: [
            {required: true, message: '', trigger: 'blur'},
            {max: 32, message: '32', trigger: 'blur'},
            {validator: check.checkFormZhSpecialCharacter, trigger: 'blur'} // ,,
          ],
          show_order: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'number', max: 9999, min: 0, message: '5', trigger: 'blur'}
          ]
        }
      }
    },
    watch: {
      tranDefForm: {
        handler (curVal) {
          // 
          if (curVal.txn_type === '0') {
            curVal.txnid = ''
          }
        },
        deep: true
      }
    },
    methods: {
      addStatParamDataChange (value) {
        this.tranDefForm.tab_disposal = value.join(',')
      }
    },
    components: {
      AllPickSelect
    }
  }
</script>

<style>

</style>



