<template>
  <div>
    <el-form label-position="right" label-width="80px" :model="userForm"
             :inline="inline" style="text-align: left">
      <el-form-item label="" prop="login_name">
        <el-input v-model="userForm.login_name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="real_name">
        <el-input v-model="userForm.real_name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="userForm.flag" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="userForm.role" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option
            v-for="item in roles"
            :key="item.role_id"
            :label="item.role_name"
            :value="item.role_id">
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div style="margin-bottom: 10px;text-align: left ">
      <el-button plain class="el-icon-plus" @click="openDialog('add')"></el-button>
      <el-button plain class="el-icon-edit" @click="openDialog('edit')"></el-button>
      <el-button plain class="el-icon-delete" @click="delUser"></el-button>
      <el-button plain class="el-icon-refresh" @click="resetPwd"></el-button>
      <el-button plain class="el-icon-setting" @click="resetFlag"></el-button>
      <el-button class="el-icon-search" type="primary" @click="selUser"></el-button>
    </div>
    <el-table
      :data="roleData"
      stripe
      border
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="35" align="left"></el-table-column>
      <el-table-column prop="login_name" label="" align="left" width="160"></el-table-column>
      <el-table-column prop="real_name" label="" align="left" width="100"></el-table-column>
      <el-table-column prop="role_name" label="" align="left" width="120"></el-table-column>
      <el-table-column prop="flag" label="" align="left" width="50"></el-table-column>
      <el-table-column prop="failed_login_attempts" label="" align="left" width="95"></el-table-column>
      <el-table-column prop="lockout" label="" align="left" width="75"></el-table-column>
      <el-table-column prop="lockout_date" label="" align="left" width="120"></el-table-column>
      <el-table-column prop="email" label="" align="left"></el-table-column>
      <el-table-column prop="mobile" label="" align="left" width="160"></el-table-column>
    </el-table>
    <el-pagination style="margin-top: 10px; text-align: right;"
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage"
                   :page-sizes="[10, 25, 50, 100]"
                   :page-size="pagesize"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="total">
    </el-pagination>
    <el-dialog :title="dialogTitle" :visible.sync="roleDialogVisible">
      <el-form :model="userDialogForm" :rules="rules" ref="userDialogForm" style=" max-height: 55vh;overflow-y: auto;">
        <el-form-item label="" :label-width="formLabelWidth" prop="login_name">
          <el-input v-model="userDialogForm.login_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" style="text-align: left;">
          <el-radio v-model="userDialogForm.flag" label="1"></el-radio>
          <el-radio v-model="userDialogForm.flag" label="2"></el-radio>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="real_name">
          <el-input v-model="userDialogForm.real_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="role" style="text-align: left;">
          <el-select v-model="userDialogForm.role" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option
              v-for="item in roles"
              :key="item.role_id"
              :label="item.role_name"
              :value="item.role_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="credentialtype" style="text-align: left;">
          <el-select v-model="userDialogForm.credentialtype" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value="0"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="4"></el-option>
            <el-option label="" value="5"></el-option>
            <el-option label="" value="6"></el-option>
            <el-option label="" value="7"></el-option>
            <el-option label="" value="8"></el-option>
            <el-option label="" value="9"></el-option>
            <el-option label="" value="10"></el-option>
            <el-option label="" value="11"></el-option>
            <el-option label="" value="12"></el-option>
            <el-option label="" value="13"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="credentialnum">
          <el-input v-model="userDialogForm.credentialnum" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="phone">
          <el-input v-model="userDialogForm.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="mobile">
          <el-input v-model="userDialogForm.mobile" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="email">
          <el-input v-model="userDialogForm.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="address">
          <el-input v-model="userDialogForm.address" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="memo">
          <el-input type="textarea" v-model="userDialogForm.memo"></el-input>
        </el-form-item>
        <el-form-item label="1" :label-width="formLabelWidth" prop="repwd" style="display: none">
          <el-input v-model="userDialogForm.repwd" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="2" :label-width="formLabelWidth" prop="password" style="display: none">
          <el-input v-model="userDialogForm.password" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="roleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('userDialogForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import check from '@/common/check'
  import ajax from '@/common/ajax'
  import crypt from '@/common/crypt'

  export default {
    created () {
      this.selUser()
      this.selRole()
    },
    methods: {
      handleSizeChange (val) {
        console.log(` ${val} `)
      },
      handleCurrentChange (val) {
        console.log(`: ${val}`)
      },
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.flag === 'add') {
              this.addUser()
            } else if (this.flag === 'edit') {
              this.updateUser()
            }
          } else {
            this.$message('')
            return false
          }
        })
      },
      bindGridData (data) {
        this.roleData = data.page.list
        this.currentPage = data.page.index
        this.pagesize = data.page.size
        this.total = data.page.total
      },
      selRole () {
        var self = this
        var option = {
          url: '/role/listNormalRole',
          success: function (data) {
            self.roles = data.row
          }
        }
        ajax.post(option)
      },
      selUser () {
        var self = this
        var option = {
          url: '/operator/list',
          param: {
            login_name: this.userForm.login_name,
            real_name: this.userForm.real_name,
            flag: this.userForm.flag,
            role: this.userForm.role,
            pageindex: 1,
            pagesize: 10
          },
          success: function (data) {
            if (data.page) {
              self.bindGridData(data)
            }
          }
        }
        ajax.post(option)
      },
      openDialog (flag) {
        let self = this
        this.flag = flag
        if (flag === 'edit') {
          this.dialogTitle = ''
          var length = this.multipleSelection.length
          if (length !== 1) {
            this.$message('')
            return
          }
          var option = {
            url: '/operator/get',
            param: {operatorId: this.multipleSelection[0].operator_id},
            success: function (data) {
              var info = data.row
              self.userDialogForm = {
                operator_id: info.operator_id,
                login_name: info.login_name,
                flag: info.flag,
                real_name: info.real_name,
                role: info.role,
                credentialtype: info.credentialtype,
                credentialnum: info.credentialnum,
                phone: info.phone,
                mobile: info.mobile,
                email: info.email,
                address: info.address,
                memo: info.memo,
                repwd: info.password,
                password: info.password
              }
            }
          }
          ajax.post(option)
        } else if (flag === 'add') {
          this.dialogTitle = ''
          this.userDialogForm = {
            operator_id: '',
            login_name: '',
            flag: '1',
            real_name: '',
            role: '',
            credentialtype: '',
            credentialnum: '',
            phone: '',
            mobile: '',
            email: '',
            address: '',
            memo: '',
            repwd: crypt.md5('123456'),
            password: crypt.md5('123456')
          }
        }
        this.roleDialogVisible = true
        if (this.$refs['userDialogForm']) {
          this.$refs['userDialogForm'].clearValidate()
        }
      },
      addUser () {
        var self = this
        var option = {
          url: '/operator/add',
          param: this.userDialogForm,
          success: function (data) {
            self.$message('')
            self.roleDialogVisible = false
            self.selUser()
          }
        }
        ajax.post(option)
      },
      updateUser () {
        var self = this
        var option = {
          url: '/operator/mod',
          param: this.userDialogForm,
          success: function (data) {
            self.$message('')
            self.roleDialogVisible = false
            self.selUser()
          }
        }
        ajax.post(option)
      },
      delUser () {
        var self = this
        var length = this.multipleSelection.length
        if (length !== 1) {
          this.$message('')
          return
        }
        var data = this.multipleSelection[0]
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var option = {
            url: '/operator/del',
            param: {
              operatorId: data.operator_id
            },
            success: function (data) {
              self.$message('')
              self.selUser()
            }
          }
          ajax.post(option)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      resetPwd () {
        var self = this
        var length = this.multipleSelection.length
        if (length !== 1) {
          this.$message('')
          return
        }
        var pwd = crypt.md5('123456') // MD5

        var data = this.multipleSelection[0]
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var option = {
            url: '/operator/reset',
            param: {
              operatorId: data.operator_id,
              loginName: data.login_name,
              passWord: pwd
            },
            success: function (data) {
              self.$message('')
              self.selUser()
            }
          }
          ajax.post(option)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      resetFlag () {
        var self = this
        var length = this.multipleSelection.length
        if (length !== 1) {
          this.$message('')
          return
        }
        var data = this.multipleSelection[0]
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var option = {
            url: '/operator/resetLoginFailedAttempts',
            param: {
              operatorId: data.operator_id
            },
            success: function (data) {
              self.$message('')
              self.selUser()
            }
          }
          ajax.post(option)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      }
    },
    data () {
      //  6
      var UserNameCheck = (rule, value, callback) => {
        var type = /^[0-9a-zA-Z]*$/
        if (type.test(value) && value.length === 6) {
          callback()
        } else {
          return callback(new Error('6'))
        }
      }
      // 
      var UserNameExist = (rule, value, callback) => {
        if (this.flag === 'edit' && this.multipleSelection[0].login_name === value) {
          callback()
        } else {
          var option = {
            url: '/operator/check/username',
            param: {'username': value}, // 
            success: function (data) { // 
              debugger
              if (data.checke_result === 'false') {
                return callback(new Error(''))
              } else {
                callback()
              }
            }
          }
          ajax.post(option)
        }
      }
      //  
      var credentialNumCheck = (rule, value, callback) => {
        var type = /^[0-9a-zA-Z]*$/
        if (type.test(value)) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }
      // 
      var credentialTypeInputCheck = (rule, value, callback) => {
        var item = this
        if (value.trim() === '' && item.userDialogForm.credentialnum !== '') { // 
          return callback(new Error(''))
        } else if (value.trim() !== '' && item.userDialogForm.credentialnum !== '') { // 
          item.$refs.userDialogForm.validateField('credentialnum')
          callback()
        } else {
          callback()
        }
      }
      // 
      var credentialNumInputCheck = (rule, value, callback) => {
        var item = this
        if (value.trim() === '' && item.userDialogForm.credentialtype !== '') { // 
          return callback(new Error(''))
        } else if (value.trim() !== '') { // 
          var credentialNum = item.userDialogForm.credentialtype.toString() // 
          if (credentialNum === '0') { // 
            var num = /^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[A-Z])$/
            if (!num.test(credentialNum)) {
              return callback(new Error(''))
            }
          }
          item.$refs.userDialogForm.validateField('credentialtype')
          callback()
        } else {
          callback()
        }
      }
      var phoneCheck = (rule, value, callback) => {
        var type = /^(([0+]\d{2,3}-)?(0\d{2,3})-)?(\d{7,8})(-(\d{3,}))?$/
        if ((value.trim() === '') || (type.test(value))) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }
      var mobileCheck = (rule, value, callback) => {
        var type = /(^13\d{9}$)|(^15\d{9}$)|(^18\d{9}$)/
        if (type.test(value)) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }

      return {
        inline: true,
        roles: [],
        userForm: {
          login_name: '',
          real_name: '',
          flag: '',
          role: ''
        },
        roleData: [{
          operator_id: '123123123123123123',
          login_name: '',
          real_name: '',
          role_name: 'tms',
          flag: '1',
          failed_login_attempts: '0',
          lockout: '0',
          lockout_date: new Date().toLocaleDateString(),
          email: '123@qwe.com',
          mobile: '13366666666'
        }],
        currentPage: 4,
        pagesize: 10,
        total: 100,
        roleDialogVisible: false,
        userDialogForm: {
          operator_id: '',
          login_name: '',
          flag: '1',
          real_name: '',
          role: '',
          credentialtype: '',
          credentialnum: '',
          phone: '',
          mobile: '',
          email: '',
          address: '',
          memo: '',
          repwd: '123456',
          password: '123456'
        },
        dialogTitle: '',
        formLabelWidth: '100px',
        rules: {
          login_name: [
            {required: true, message: '', trigger: 'blur'},
            {len: 6, message: '6', trigger: 'blur'},
            {validator: UserNameCheck, trigger: 'blur'}, // 
            {validator: UserNameExist, trigger: 'blur'} // 
          ],
          real_name: [
            {required: true, message: '', trigger: 'blur'},
            {max: 50, message: '50', trigger: 'blur'},
            {validator: check.checkFormSpecialCode, trigger: 'blur'} // 
          ],
          role: [
            {required: true, message: '', trigger: 'blur'}
          ],
          credentialtype: [
            {validator: credentialTypeInputCheck, trigger: 'blur'} // 
          ],
          credentialnum: [
            {max: 50, message: '32', trigger: 'blur'},
            {validator: credentialNumCheck, trigger: 'blur'}, // 
            {validator: credentialNumInputCheck, trigger: 'blur'} // 
          ],
          phone: [
            {validator: phoneCheck, trigger: 'blur'} // ,,
          ],
          mobile: [
            {required: true, message: '', trigger: 'blur'},
            {validator: mobileCheck, trigger: 'blur'} // 
          ],
          email: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur'},
            {max: 50, message: '50', trigger: 'blur'}
          ],
          address: [
            {max: 200, message: '200', trigger: 'blur'},
            {validator: check.checkFormSpecialCode, trigger: 'blur'} // 
          ],
          memo: [
            {max: 300, message: '300', trigger: 'blur'},
            {validator: check.checkFormSpecialCode, trigger: 'blur'} // 
          ]
        },
        multipleSelection: [],
        flag: ''
      }
    }
  }
</script>

<style>

</style>



